              className="mySwiper"
            >
              <SwiperSlide>
                <div className="description-certificates">
                  <img src={logoCompany} alt="logoCompany" />
                </div>
              </SwiperSlide>
              <SwiperSlide>
                <div className="description-certificates">
                  <img src={logoCompany} alt="logoCompany" />
                </div>
